.gpt3__Possibility {
  display: flex;
  flex-direction: row;
}

.gpt3__Possibility-image {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 2rem;

}

.gpt3__Possibility-image img {
  width: 100%;
  height: 100%;
}

.gpt3__Possibility-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}

.gpt3__Possibility-content h4 {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #71E5FF;
}

.gpt3__Possibility-content h4:last-child {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  /* identical to box height, or 188% */


  color: #FF8A71;
}

.gpt3__Possibility-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
  margin: 1rem 0;
}

.gpt3__Possibility-content p {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: var(--color-text);
  margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {
  .gpt3__Possibility {
    flex-direction: column;
  }
  .gpt3__Possibility-image{
    margin: 1rem 0;
  }
  .gpt3__Possibility-content{
    margin-top: 2rem;
  }
}
